<template>
  <div class="examinee">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs v-model="table.query.recruitType" @tab-click="handleClick">
        <el-tab-pane label="文化招生" name="CULTURE"></el-tab-pane>
        <el-tab-pane label="特长招生" name="SPECIALITY"></el-tab-pane>
      </el-tabs>
      <div class="btn-block">
        <div @click="showImpDialog" class="btn-main ml15">导入</div>
        <div @click="$exportDetail('/export/rStudentExport.do')" class="btn-main ml15">导出</div>
        <div @click="goToReplenish(0)" class="btn-main-deepen ml15">补录考生</div>
        <div @click="showDialog()" class="btn-main-deepen ml15 mr5">设置考试批次</div>
        <el-tooltip content="报考类别需一致" placement="top" effect="dark">
          <i class="el-icon-warning"></i>
        </el-tooltip>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
        <table class="table">
          <thead>
            <tr>
              <th width="4%">
                <el-checkbox v-show="checkAll !== null" v-model="checkAll"></el-checkbox>
                <span v-show="checkAll === null">-</span>
              </th>
              <th width="8%">姓名</th>
              <th width="15%">身份证号码</th>
              <th width="14%">报考类别</th>
              <th v-if="table.query.recruitType !== 'CULTURE'" width="13%">特长类别/专业</th>
              <th width="12%">联系电话</th>
              <th width="15%">考试批次</th>
              <th width="15%">考点</th>
              <th width="7%">状态</th>
              <th width="10%">报名时间</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td>
              <el-checkbox v-show="item.studentStatus === 'APPLIED'" v-model="checkboxList[key]"></el-checkbox>
              <span v-show="item.studentStatus !== 'APPLIED'">-</span>
            </td>
            <td>
              <span class="oper hand" @click="goToReplenish(item.id)">{{item.name}}</span>
            </td>
            <td>{{item.idCard || '--'}}</td>
            <td>{{item.firstRecruitType}}<span v-show="table.query.recruitType === 'CULTURE'">/</span>{{item.secondRecruitType}}
            </td>
            <td v-show="table.query.recruitType !== 'CULTURE'">
              {{item.firstSpecialityType}}/{{item.secondSpecialityType}}
            </td>
            <td>{{item.phone}}</td>
            <td>{{item.batchName || '--'}}</td>
            <td>{{item.examCentreName|| '--'}}</td>
            <td>{{$enums.RecruitStudentStatus.getName(item.studentStatus)}}</td>
            <td>{{item.applyTime | substrTime}}</td>
          </tr></tbody>
        </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog title="设置考试批次" class="dialog" :visible.sync="dialog" width="450px">
        <span class="title">考试批次：</span>
        <el-select v-model="batchForm.batchId" default-first-option filterable class="input">
          <el-option
              v-for="item in batchList"
              :key="item.id"
              :label="item.title"
              :value="item.id">
          </el-option>
        </el-select>
        <div class="btns">
          <div @click="setBatch" class="btn-submit mr20">确认</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <el-dialog title="批量导入" class="dialog" :visible.sync="impDialog" width="500px">
        <el-form v-if="impDialog" :model="impForm" :rules="impRules" label-position="left" ref="form" label-width="80px" class="form">
          <el-form-item label="考试批次" prop="batchId">
            <el-select @change="getExamCenter(impForm.batchId)"
                       default-first-option clearable filterable class="input" v-model="impForm.batchId"
                       placeholder="请选择">
              <el-option v-for="item in batchList"
                         :value="item.id" :key="item.id" :label="item.title"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="考点" prop="examCentreId">
            <el-select filterable default-first-option class="input" clearable
                       v-model="impForm.examCentreId"
                       placeholder="请选择">
              <el-option v-for="item in examCenterList"
                         :value="item.id" :key="item.id" :label="item.name"
              ></el-option>
            </el-select>
            <el-tooltip content="请先选择考试批次" placement="top" effect="dark">
              <i class="el-icon-warning ml10"></i>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="上传文件" class="form-impt">
            <el-input type="file" class="input file-input" ref="upload"
                      accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                      placeholder="请选择"></el-input>
            <a href="../gy-files/template/批量导入考生模板.xlsx" download  class="f-small main-c ml5">下载模板</a>
          </el-form-item>
        </el-form>
        <div class="btns">
          <div @click="impFun" class="btn-submit btn-upload mr20">提交</div>
          <div @click="impDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data() {
      return {
        nav: [{name: "考生信息", path: ""}],
        hide: true,
        searchData: [
          {type: 'select', title: '状态', model: 'studentStatus', option: this.$enums.RecruitStudentStatus.list},
          {type: 'cascader', title: '报考类别', modelName: ['firstRecruitTypeId', 'secondRecruitTypeId'], option: []},
          {
            type: 'cascader', title: '特长类别',
            hide: true,
            modelName: ['firstSpecialityTypeId', 'secondSpecialityTypeId'], option: []
          },
          {type: 'input', placeholder: '姓名/身份证号码'}
        ],
        dialog: false,
        // 所有的批次
        batchList: [],
        table: {
          url: '/rstudent/queryStudentPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            firstRecruitTypeId: null,
            secondRecruitTypeId: null,
            firstSpecialityTypeId: null,
            secondSpecialityTypeId: null,
            recruitType: this.$route.query.type || 'CULTURE',
            studentStatus: null
          }
        },
        // 批量设置考试批次
        batchForm: {
          batchId: null,
          studentIdList: []
        },
        // 表格多选框
        checkboxList: [false],
        // 导入弹窗
        impDialog: false,
        // 导入参数
        impForm: {
          batchId: '',
          examCentreId: '',
          file: ''
        },
        // 导入参数校验
        impRules: {
          batchId: {required: true, message: '请选择考试批次', trigger: 'change'},
          examCentreId: {required: true, message: '请选择考点', trigger: 'change'},
        },
        // 考点列表
        examCenterList: []
      }
    },
    watch: {
      'table.list': function () {
        this.checkboxList = new Array(this.table.list.length).fill(false)
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if (this.table.list.length < 1) {
            return false
          } else if (this.table.list.every(item => item.studentStatus !== 'APPLIED')) {
            return null
          }
          for (let i = 0; i < this.table.list.length; i++) {
            let n = this.table.list[i]
            if (n.studentStatus === 'APPLIED' && this.checkboxList[i] === false) {
              return false
            }
          }
          return true
        },
        set: function (val) {
          let arr = []
          for (let i = 0; i < this.table.list.length; i++) {
            let n = this.table.list[i]
            if (n.studentStatus === 'APPLIED') {
              arr[i] = val
            } else {
              arr[i] = false
            }
          }
          this.checkboxList = arr
        }
      }
    },
    methods: {
      // 展现导入弹窗
      showImpDialog() {
        this.impDialog = true
        this.impForm.batchId = null
        this.impForm.examCentreId = null
        this.getAllBatchList()
      },
      // 导入考生
      async impFun() {
        await this.$refs.form.validate()
        let file = this.$refs.upload.$refs.input.files[0]
        let formData = new FormData()
        formData.append('file', file)
        formData.append('batchId', this.impForm.batchId)
        formData.append('examCentreId', this.impForm.examCentreId)
        let obj = {
          url: '/import/importStudentList.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if(res.errMsgList && res.errMsgList.length){
          this.$message.error(res.errMsgList[0])
        }else{
          this.$remind('导入成功')
        }
        this.impDialog = false
        this.$search(this.table)
      },
      // 获取文化生报考类别
      async getCultureType() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'RECRUIT_CULTURE'
          }
        })
        this.searchData[1].option = []
        res.forEach(async (e, i) => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id,
            children: []
          })

          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query: {
              parentId: e.id,
              dictType: e.dictType
            }
          })
          res.forEach(n => {
            this.searchData[1].option[i].children.push({
              label: n.name,
              value: n.id
            })
          })
        })

      },
      // 获取特长生报考类别
      async getSpecialtyType() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'RECRUIT_SPECIALITY'
          }
        })
        this.searchData[1].option = []
        res.forEach(e => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id
          })
        })
      },
      // 获取特长类别
      async getProfessionalList() {
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query: {
            dictType: 'SPECIALITY'
          }
        })
        this.searchData[2].option = []
        res.forEach(async (e, i) => {
          this.searchData[2].option.push({
            label: e.name,
            value: e.id,
            children: []
          })
          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query: {
              parentId: e.id,
              dictType: e.dictType
            }
          })
          res.forEach(n => {
            this.searchData[2].option[i].children.push({
              label: n.name,
              value: n.id
            })
          })
        })
      },
      // 获取报考类别下的考试批次
      async getBatchList(firstId, secondId) {
        this.batchList = await this.$ajax({
          url: '/rbatch/queryBatchListByRecruitType.do',
          query: {
            recruitType: this.table.query.recruitType,
            firstRecruitTypeId: firstId,
            secondRecruitTypeId: secondId
          }
        })
      },
      // 获取所有的考试批次
      async getAllBatchList() {
        this.batchList = await this.$ajax({
          url: '/rbatch/queryBatchList.do',
          query: {
            recruitType: this.table.query.recruitType,
          }
        })
      },
      async getExamCenter(id) {
        if(this.impForm.batchId){
          this.impForm.examCentreId = null
          this.examCenterList = await this.$ajax({
            url: '/centre/queryExamCentreList.do',
            query: {
              batchId: id,
            }
          })
        }
      },
      // 展现设置考试批次弹窗
      showDialog() {
        if (!this.checkboxList.some(e => e)) {
          this.$message.warning('请先选择考生')
          return
        }
        let index = this.checkboxList.indexOf(true)
        let id = this.table.list[index].secondRecruitTypeId
        for (let i = 0; i < this.table.list.length; i++) {
          let e = this.table.list[i]
          if (this.checkboxList[i] && e.secondRecruitTypeId !== id) {
            this.$message.warning('考生报考类别不一致')
            return
          }
        }
        let secondId = this.table.list[index].secondRecruitTypeId
        let firstId = this.table.list[index].firstRecruitTypeId
        this.getBatchList(firstId, secondId)
        this.batchForm.batchId = null
        this.dialog = true
      },
      // 前往新增 编辑考生
      goToReplenish(id) {
        this.$router.push({
          path: `/recruit/examinee/${id}`,
          query: {
            type: this.table.query.recruitType
          }
        })
      },
      // 设置考试批次
      async setBatch() {
        if (!this.batchForm.batchId) {
          this.$message.warning('请选择考试批次')
          return
        }
        let arr = []
        this.table.list.forEach((e, i) => {
          if (this.checkboxList[i]) {
            arr.push(e.id)
          }
        })
        await this.$ajax({
          url: '/rbatch/batchDistributionBatchStudent.do',
          query: {
            batchId: this.batchForm.batchId,
            studentIdList: arr
          }
        })
        this.$remind('设置成功')
        this.dialog = false
        this.$search(this.table)
      },
      handleClick() {
        this.table.query.firstRecruitTypeId = null
        this.table.query.secondRecruitTypeId = null
        if (this.table.query.recruitType === 'SPECIALITY') {
          this.$router.push({path: '/recruit/examinee',query:{
              type: 'SPECIALITY'
            }})
          this.getSpecialtyType()
          this.searchData[2].hide = false
        } else {
          this.$router.push({path: '/recruit/examinee',query:{
              type: 'CULTURE'
            }})
          this.searchData[2].hide = true
          this.getCultureType()
        }
        this.$search(this.table)
      }
    },
    async mounted() {
      if (this.table.query.recruitType === 'SPECIALITY') {
        this.getSpecialtyType()
      }
      this.getProfessionalList()
      this.getCultureType()
      await this.$search(this.table)
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
    }
  }
</script>
<style lang="sass" scoped>
  .examinee
    .hand
      padding: 0px
    .btn-block
      i
        line-height: 30px
        display: inline-block
        float: right
        margin-top: 2px
        margin-right: 5px
    .dialog
      .title
        width: 80px
        display: inline-block
      .input
        width: 325px
        display: inline-block
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .btns
        margin-top: 35px
        text-align: center
</style>
